import ourColors from "../inc/fitnessColors"
import {link} from "@wordpress/icons"
import { ToolbarGroup, ToolbarButton, Popover , Button, PanelBody,PanelRow, ColorPalette} from "@wordpress/components"
import {RichText, InspectorControls, BlockControls, __experimentalLinkControl as LinkControl, getColorObjectByColorValue} from "@wordpress/block-editor"
import { registerBlockType } from "@wordpress/blocks"
import { useState } from "@wordpress/element"


registerBlockType("fitnessblocktheme/fitnessbutton",{
    title:"Button",
    attributes:{
        text:{type:"string"},
        linkObject:{type:"object",default:{url:""}},
        colorName: { type: "string",default:"Primary Button"},
        colorClass: { type: "string",default:"primaryButton"}
    },
    edit:EditComponent,
    save:SaveComponent
})
function EditComponent(props){
    const [isLinkPickerVisible, setIsLinkPickerVisible]=useState(false);

    function handleTextChange(x){
        props.setAttributes({text:x});
    }

    function buttonHandler(){
        setIsLinkPickerVisible(prev => !prev)
    }

    function handleLinkChange(newLink){
        props.setAttributes({linkObject:newLink})
    }
    

    const currentColorValue = ourColors.filter(color=>{
        return color.name== props.attributes.colorName
    })[0].color
    
    function handleColorChange(colorCode) {
        // from the hex value that the color palette gives us, we need to find its color name
        const { name,cls }=getColorObjectByColorValue(ourColors,colorCode)
        props.setAttributes({ colorName: name })
        props.setAttributes({ colorClass: cls })
      }
       
    return (
        <> 
            <BlockControls>
                <ToolbarGroup>
                    <ToolbarButton onClick={buttonHandler} icon={link}/>
                </ToolbarGroup>
            </BlockControls>

            <InspectorControls>
                <PanelBody title="Color" initialOpen="true">
                    <PanelRow>
                        <ColorPalette disableCustomColors={true} clearable={false} colors={ourColors} value={currentColorValue} onChange={handleColorChange}/>
                    </PanelRow>
                </PanelBody>
            </InspectorControls>
            
            <RichText allowedFormats={[]} tagName="a" className={`global-btn2 d-flex align-items-center text-center text-md-start ${props.attributes.colorClass}`} value={props.attributes.text} onChange={handleTextChange}/>
            
            {isLinkPickerVisible && (
                <Popover position="bottom center" onFocusOutside={() => setIsLinkPickerVisible(false)}>
                    <LinkControl settings={[]}  value={props.attributes.linkObject} onChange={handleLinkChange}/>
                    <button className="btn btn-primary" variant="primary" onClick={()=>setIsLinkPickerVisible(false)} style={{display:"block",width:"100%"}}>Save Link</button>
                </Popover>
            )}
        </>
    );
}
function SaveComponent(props){
    return (<a href={props.attributes.linkObject.url} className={`global-btn2 d-flex align-items-center text-center text-md-start ${props.attributes.colorClass}`}>{props.attributes.text}</a>)
}